<template>
    <h1>Lista de Estudiantes VUE.JS</h1>
    <div>
        <table class="table">
            <thead>
                <tr class="bg-success text-white">
                    <th scope="col">#</th>
                    <th scope="col">ID</th>
                    <th scope="col">Nombre</th>
                    <th scope="col">Apellido</th>
                    <th></th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(dato, index) of datos" :key="dato.id" class="bg-dark">
                    <th scope="row" class="border border-1"><span class="text-primary">{{index+1}}</span></th>
                    <td class="text-info border border-1">{{dato.id}}</td>
                    <td class="text-info">{{dato.nombre}}</td>
                    <td class="text-info">{{dato.apellido}}</td>
                    <td class="border border-1">
                        <button class="btn btn-warning btn-sm" @click="verdatos(dato)">Editar</button>
                        <button class="btn btn-danger btn-sm ms-1" @click="Borrar(dato.id)">Borrar</button>
                    </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>

import {mapState, mapActions, mapMutations} from 'vuex';

export default {
    computed:{
        ...mapState(['datos','mostrar'])
    },
    methods:{
        ...mapMutations(['dataitem','upMostrar']),
        ...mapActions(['Borrar']),
        verdatos(dato){
            let nuevo_objeto = {
                id: dato.id,
                nombre : dato.nombre,
                apellido: dato.apellido
            };
            this.dataitem(nuevo_objeto);
            let mostrar__ = true //este mostrar es una variable interna de la funcion.
            this.upMostrar(mostrar__); // y activa boton Guardar       
        }
    }
}
</script>

