<template>

    <div>
        <form class="text-start">
            <div class="mb-3">
                <label for="inputNombre" class="form-label">Nombre</label>
                <input type="text" class="form-control" id="inputNombre" v-model="obj.nombre" v-show="!mostrar">
                <input type="text" class="form-control" v-model="objeto_editar.nombre" v-show="mostrar">
            </div>
            <div class="mb-3">
                <label for="inputApellido" class="form-label">Apellido</label>
                <input type="text" class="form-control" id="inputApellido" v-model="obj.apellido" v-show="!mostrar">
                <input type="text" class="form-control" v-model="objeto_editar.apellido" v-show="mostrar">
            </div>
            <div class="text-end" v-show="mostrar">
                <button class="btn btn-primary" @click.prevent="guardar();">Guardar</button>
            </div>
            <div class="text-start" v-show="!mostrar">
                <button class="btn btn-success" @click.prevent="resul(obj)">Agregar</button>
            </div>
        </form>
    </div>

</template>

<script>

import {mapState, mapMutations, mapActions} from 'vuex'

const shortid = require ('shortid'); //generador de id 

export default {
    name: 'Formulario',
    data(){
        return{
            obj:{
                id:'',
                nombre: '',
                apellido: '',
            }
        }
    },
    computed:{
        ...mapState(['mostrar','objeto_editar','objeto_final'])
    },
    methods:{
        ...mapMutations(['nuevoupdate','upMostrar']),
        ...mapActions(['Agregar','Editar']),
        resul(){   
            if(this.obj.nombre.trim() === '' || this.obj.apellido.trim() === ''){ //si el nombre es igual a vacio?--->
                alert('Estimad@ usuari@, Complete todos los datos')
                return //sin el return, se envian igual los datos
            }
            //let id = this.datos.length + 1   //muestra la cantidad de elementos en el array   
            //let obj = {i:id, n:this.nombre, a:this.apellido}; //obj sera el parametro que se envia a store
            this.Agregar(this.obj)        //este parametro lo recibe payload
            this.obj.id = shortid.generate()
            console.log('objcomponente',this.obj)
            this.obj = { //input clear
                id:'',
                nombre: '',
                apellido: '',
            }
        },
        guardar(){
            let dato_actualizado = {
                'id': this.objeto_editar.id,
                'nombre': this.objeto_editar.nombre,
                'apellido': this.objeto_editar.apellido
            }
            this.nuevoupdate(dato_actualizado)
            this.Editar(this.objeto_final)
            let mostrar__ = false //activa boton Agregar
            this.upMostrar(mostrar__); 
        }
    }
}
</script>

