import { createStore } from 'vuex'

export default createStore({
  state: {
    datos:[],
    mostrar: false,
    objeto_editar: {},
    objeto_final: {}
  },
  mutations: {
    agregar(state, payload){
      state.datos.push(payload) //se pushea payload en array 'datos' 
      console.log('payload = obj',payload)//payload viene como parametro de la funcion desde el componente
      console.log('array datos push:', state.datos)
    },
    borrar(state, payload){
      console.log('payload = id borrado=>>>',payload)
      state.datos = state.datos.filter(ele => ele.id !== payload)
      console.log('nuevo array datos>>>',state.datos)
      //filter crea un nuevo array, y la condicion es que sus elementos
      //sean distintos al parametro seleccionado
    },
    dataitem(state, payload){ //recibimos el objeto a editar
      state.objeto_editar = payload
    },
    upMostrar(state, payload){ //muestra el input para editar
      state.mostrar = payload
    },
    nuevoupdate(state, payload){ //cargamos los datos ya editados
      state.objeto_final = payload
    },
    editar(state, payload){
      console.log('editar payload',payload)
      console.log('id editar',state.objeto_final.id)
      state.datos = state.datos.filter(ele => ele.id !== state.objeto_final.id)
      state.datos.push(state.objeto_final)
    }
  },

  actions: {
    Agregar({ commit }, dato) {
      commit('agregar', dato)
    },

    Borrar({ commit }, ide) {
      commit('borrar', ide)
    },

    Editar({ commit }, dato){
      commit('editar', dato)
    }
  },
  modules: {
  }
})
